// Form
//
// Styleguide 15.0.0

// 
.form-item {
  padding: 20px 20px 30px 20px;
  border-top: 1px solid $c_k;
  box-sizing: border-box;
  dd {
    @extend %clearfix;
  }
  &:last-child {
    padding-bottom: 0;
  }
  @include mqSP {
    padding-left: calc-vw(30);
    padding-right: calc-vw(30);
  }
  .page-reminder &:last-child,
  .page-login &:last-child {
    padding-bottom: 30px;
    @include mqSP {
      padding-bottom: calc-vw(60);
    }
  }
}
.form-item_confirm {
  @extend .form-item;
  .page-contact & {
    padding: 0;
    border: none;
  }
}
// 
.form-item_reference {
  padding-top: 0;
  border-top: none;
  @extend .form-item;
}

//  ()
.form-subItems {
  background: $c_gray_bg;
  .form-item {
    border-color: $c_gray;
    &:last-child {
      padding-bottom: 30px;
    }
  }
}

// input
.form-item-wrap {
  @extend %clearfix;
  font-size: 25px;
  &+.text-formError {
    &:first-of-type {
      margin-top: 10px;
    }
  }
  & + .form-item-wrap {
    margin-top: 20px;
  }
  @include mqTB {
    font-size: calc-vw(25, 1280);
  }
}
.form-item-inline {
  input, select, span {
    display: inline;
  }
  input, select {
    margin-left: 20px;
    &:first-child {
      margin-left: 0;
    }
  }
  span {
    @include f_chikumin;
    font-size: 25px;
  }
  @include mqSP {
    input, select {
      margin-left: 10px;
    }
  }
}
.form-item_2cal {
  float: left;
  width: 49%;
  &:nth-child(2n+1) {
    margin-right: 2%;
  }
}
.form-item_3cal {
  width: calc( (100% - 60px) / 3);
  margin: 0 0 0 30px;
  box-sizing: border-box;
  @include mqSP {
    width: calc( (100% - 20px) / 3);
    margin: 0 0 0 10px;
  }
  &:nth-child(3n+1) {
    margin-left: 0;
  }
}


// Text
//
// 
//
// Markup:
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <div class="form-item_2cal">
//       <input type="text" placeholder="" />
//       <p class="text-formError"></p>
//     </div>
//     <div class="form-item_2cal">
//       <input type="text" placeholder="" />
//       <p class="text-formError"></p>
//     </div>
//   </dd>
// </dl>
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <div class="form-item-inline">
//       <input type="text" placeholder="xxxx" class="input_s" /><span></span>
//       <input type="text" placeholder="xx" class="input_ss" /><span></span>
//       <input type="text" placeholder="xx" class="input_ss" /><span></span>
//     </div>
//   </dd>
// </dl>
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <input type="text" placeholder="" />
//     <p class="text-formError"></p>
//     <p class="text-formError"></p>
//   </dd>
// </dl>
//
// Styleguide 15.1.0
%input_text {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 25px;
  transition: all .3s $tr_easing;
  @include f_base;
  background: transparent;
  &:focus,
  &:hover {
    background: $c_gray_light;
  }
  @include placeholder {
    color: $c_gray_placeholder;
    @include f_base;
  }
  @include mqTB {
    font-size: calc-vw(25, 1280);
  }
}
.form-item {
  input[type="text"],
  input[type="number"],
  input[type="email"],
  input[type="password"],
  input[type="tel"] {
    @extend %input_text;
    &.input_s {
      width: 70px;
    }
    &.input_ss {
      width: 50px;
    }
  }
}

// Radio
//
// 
//
// Markup:
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <div class="form-item-wrap">
//       <div class="form-item_3cal form-item_radio"><input type="radio" name="gender" value="" /><label></label></div>
//       <div class="form-item_3cal form-item_radio"><input type="radio" name="gender" value="" /><label></label></div>
//       <div class="form-item_3cal form-item_radio"><input type="radio" name="gender" value="" /><label></label></div>
//     </div>
//     <p class="text-formError"></p>
//   </dd>
// </dl>
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <div class="form-item-wrap">
//       <div class="form-item_2cal form-item_radio"><input type="radio" name="mailmagazine" value="" checked /><label></label></div>
//       <div class="form-item_2cal form-item_radio"><input type="radio" name="mailmagazine" value="" /><label></label></div>
//     </div>
//     <p class="text-formError"></p>
//   </dd>
//   <dd class="text_formNote"></dd>
// </dl>
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <div class="form-item-wrap">
//       <div class="form-item_3cal form-item_radio_l"><input type="radio" name="size" value="S" /><label>S</label></div>
//       <div class="form-item_3cal form-item_radio_l"><input type="radio" name="size" value="M" /><label>M</label></div>
//       <div class="form-item_3cal form-item_radio_l"><input type="radio" name="size" value="L" /><label>L</label></div>
//     </div>
//     <p class="text-formError"></p>
//   </dd>
// </dl>
//
// Styleguide 15.2.0
%input_radio {
  position: relative;
  float: left;
  height: 50px;
  label {
    display:block;
    height: 50px;
    border-radius: 8px;
    border: 2px dashed $c_gray_placeholder;
    @include f_chikumin;
    font-size: 20px;
    color: $c_gray_placeholder;
    text-align: center;
    line-height: 44px;
    transition: all .3s $tr_easing;
    box-sizing: border-box;
    @include mqTB {
      font-size: calc-vw(20, 1280);
    }
  }
  input[type="radio"] {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index:2;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    &:checked + label {
      border: 2px solid $c_k;
      color: $c_k;
    }
  }
}
// 
.form-item_radio {
  @extend %input_radio;
}
.form-item_radio_l {
  @extend %input_radio;
  height: 100px;
  label {
    height: 100px;
    line-height: 96px;
    font-size: 25px;
  }
}
.form-item_radio_block {
  @extend %input_radio;
  float: none;
  display: block;
  margin-bottom: 20px;
  line-height: 1.5;
  height: auto;
  &:last-child {
    margin-bottom: 0;
  }
  label {
    text-align: left;
    padding: 0 20px;
    line-height: 1.5;
    padding-top: 8px;
    padding-bottom: 8px;
    height: auto;
  }
}

// Check
//
// 
//
// Markup:
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <div class="form-item_check form-item-wrap">
//       <input type="checkbox" class="js-checkToAccordion" data-target="js-accordionBody"><label></label>
//     </div>
//     <p class="text-formError"></p>
//   </dd>
// </dl>
// <dl class="form-item">
//   <dd>
//     <div class="form-item_check_s">
//       <input type="checkbox"><label></label>
//     </div>
//   </dd>
// </dl>
// <dl class="form-item_confirm">
//   <dd>
//     <div class="form-item_check form-item-wrap">
//       <input type="checkbox"><label></label>
//     </div>
//   </dd>
// </dl>
//
// Styleguide 15.3.0
%input_check {
  display: table;
  position: relative;
  width: 100%;
  // @include mqSP {
  //   display: table;
  // }
  label {
    // display: block;
    display: table-cell;
    vertical-align: middle;
    z-index: 1;
    height: 50px;
    padding: 0 0 0 60px;
    @include f_chikumin;
    font-size: 16px;
    // line-height: 50px;
    cursor: pointer;
    position: relative;
    // @include mqTB {
    //   line-height: 46px;
    // }
    @include mqSP {
      font-size: 14px;
    }
    &:before,
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 2px;
      width: 50px;
      height: 50px;
      box-sizing: border-box;
      transition: all .3s $tr_easing;
      cursor: pointer;
    }
    &:before {
      border-radius: 8px;
      border: 2px dashed $c_gray;
    }
    &:after {
      background-image: url(../img/icon-checked.svg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: auto 24px;
      opacity: 0;
    }
  }
  input[type="checkbox"] {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    &:checked + label {
      &:before {
        border: 2px solid $c_k;
      }
      &:after {
        opacity: 1;
      }
    }
  }
}
.form-item_check {
  @extend %input_check;
  .page-contact & {
    margin-top: 30px;
    text-align: center;
    @include mqSP {
      text-align: left;
    }
    label {
      display: inline-block;
      a {
        color: $c_link;
        text-decoration: underline;
      }
    }
  }
}
.form-item_confirm {
  @extend %input_check;
  .form-item_check {
    margin-bottom: 30px;
    text-align: center;
    label {
      display: inline-block;
      position: relative;
      line-height: 50px;
      height: 50px;
    }
    @include mqTB {
      label {
        line-height: 46px;
      }
    }
    @include mqSP {
      text-align: left;
      label {
        display: table-cell;
        line-height: 1.5;
      }
    }
  }
}
.form-item_check_s {
  @extend %input_check;
  label {
    height: 25px;
    padding: 0 0 0 35px;
    line-height: 25px;
    &:before,
    &:after {
      width: 25px;
      height: 25px;
    }
    &:before {
      border-radius: 5px;
    }
    &:after {
      background-size: auto 12px;
    }
  }
}

