//       </select>
//     </div>
//     <p class="text-formError"></p>
//   </dd>
// </dl>
//
// Styleguide 15.4.0
%input_select {
  position: relative;
  width: 100%;
  height: 50px;
  font-size: 25px;
  line-height: 50px;
  @include transition;
  @include f_chikumin;
  background: transparent;
  @include mqTB {
    font-size: calc-vw(25, 1280);
  }
  select {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    opacity: 0;
    font-size: 25px;
  }
  p {
    color: $c_gray_placeholder;
    transition: all .3s $tr_easing;
    white-space: nowrap;
    overflow: hidden;
    width: calc(100% - 20px);
    &.is-selected {
      color: $c_k;
    }
    &:after {
      content: "";
      position: absolute;
      right: 0;
      top: calc(50% - 4px);
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 8px 5px 0 5px;
      border-color: $c_k transparent transparent transparent;
    }
  }
  &:hover {
    background: $c_gray_light;
  }

}
.form-item_select {
  @extend %input_select;
}

// Text
//
// 
//
// Markup:
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <div class="form-item-wrap">
//       <textarea class="form-item_textarea" placeholder=""></textarea>
//     </div>
//   </dd>
// </dl>
//
// Styleguide 15.5.0
%input_textarea {
  display: block;
  width: 100%;
  height: 200px;
  font-size: 25px;
  transition: all .3s $tr_easing;
  @include f_base;
  background: transparent;
  &:focus,
  &:hover {
    background: $c_gray_light;
  }
  @include placeholder {
    color: $c_gray_placeholder;
    @include f_base;
  }
  @include mqTB {
    font-size: calc-vw(25, 1280);
  }
}
.form-item_textarea {
  @extend %input_textarea;
}


// Confirm
//
// 
//
// Markup:
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <p class="form-item-confirm"> </p>
//   </dd>
// </dl>
// <dl class="form-item">
//   <dt class="heading_formTitle"><span></span></dt>
//   <dd>
//     <p class="form-item-confirm_s"></p>
//   </dd>
// </dl>
//
// Styleguide 15.6.0
.form-item-confirm {
  font-size: 25px;
  @include f_chikumin;
  word-break: break-all;
  @include mqTB {
    font-size: calc-vw(25, 1280);
  }
}
.form-item-confirm_s {
  font-size: 15px;
  @include f_chikumin;
  word-break: break-all;
  @include mqTB {
    font-size: calc-vw(15, 1280);
  }
  @include mqSP {
    font-size: 14px;
  }
}