// Styleguide 14.1.0
.table_simple {
  width: 100%;
  border-left: 1px solid $c_gray;
  border-top: 1px solid $c_gray;
  th,
  td {
    padding: 15px 0;
    border-right: 1px solid $c_gray;
    border-bottom: 1px solid $c_gray;
    font-size: 14px;
    text-align: center;
  }
  thead {
    th {
      background: $c_gray4;
      font-weight: bold;
      &:first-child {
        background: $c_gray_bg;
      }
    }
  }
  tbody {
    th {
      background: $c_gray_bg;
    }
  }
}

.table_wrap{
  @include mqSP{
    overflow-x: scroll;
    margin-bottom: calc-vw(35,320);
  }
}